"use client";

import { useState } from "react";
import { LearningFeed } from "@/actions/LearningFeed";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Loader2, Sparkles, Library, BookOpen } from "lucide-react";
import { toast } from "sonner";

export default function LearningFeedPage() {
  const [industry, setIndustry] = useState("");
  const [skills, setSkills] = useState("");
  const [interests, setInterests] = useState("");
  const [feed, setFeed] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleGenerateFeed = async () => {
    setLoading(true);
    setFeed([]);
    try {
      if (!industry && !skills && !interests) {
        toast.error("Please fill in at least one field.");
        setLoading(false);
        return;
      }

      const result = await LearningFeed({
        industry,
        skills: skills.split(",").map((s) => s.trim()).filter(Boolean),
        interests: interests.split(",").map((i) => i.trim()).filter(Boolean),
      });

      setFeed(result);
      toast.success("Your feed has been generated!");
    } catch (err) {
      console.error(err);
      toast.error(err.message || "Failed to generate feed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto max-w-5xl py-8 space-y-8">
      {/* Header */}
      <div className="text-center mb-10">
        <h1 className="text-3xl md:text-5xl font-bold gradient-title">
          ðŸ“š Your AI Learning Feed
        </h1>
        <p className="text-lg text-muted-foreground mt-2 max-w-2xl mx-auto">
          Enter your industry, skills, and interests to generate a curated feed.
        </p>
      </div>

      {/* Form */}
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl">Customize Your Feed</CardTitle>
          <CardDescription>Tell us what you want to learn.</CardDescription>
        </CardHeader>
        <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <Label htmlFor="industry">Industry</Label>
            <Input id="industry" value={industry} onChange={(e) => setIndustry(e.target.value)} placeholder="e.g., AI" />
          </div>
          <div>
            <Label htmlFor="skills">Skills (comma-separated)</Label>
            <Textarea id="skills" value={skills} onChange={(e) => setSkills(e.target.value)} placeholder="e.g., Python, PyTorch" className="h-24" />
          </div>
          <div className="md:col-span-2">
            <Label htmlFor="interests">Interests (comma-separated)</Label>
            <Textarea id="interests" value={interests} onChange={(e) => setInterests(e.target.value)} placeholder="e.g., Generative Art, Robotics" className="h-24" />
          </div>
        </CardContent>
        <CardFooter>
          <Button onClick={handleGenerateFeed} disabled={loading}>
            {loading ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <Sparkles className="mr-2 h-5 w-5" />}
            {loading ? "Generating..." : "Generate Feed"}
          </Button>
        </CardFooter>
      </Card>

      {/* Feed */}
      <div className="space-y-6">
        {loading && (
          <div className="text-center p-8 text-muted-foreground">
            <Loader2 className="h-16 w-16 animate-spin mx-auto mb-4" />
            Generating your feed...
          </div>
        )}

        {!loading && feed.length === 0 && (
          <div className="text-center p-8 text-muted-foreground border-2 border-dashed rounded-lg">
            <Library className="h-16 w-16 mb-4 mx-auto" />
            <p>Your feed is empty. Fill out the form above to start.</p>
          </div>
        )}

        {!loading && feed.length > 0 && (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {feed.map((item, idx) => (
              <Card key={idx} className="shadow-md hover:shadow-lg">
                <CardHeader>
                  <CardTitle>{item.title}</CardTitle>
                  <CardDescription>{item.type} â€¢ {item.source} â€¢ {item.time}</CardDescription>
                </CardHeader>
                <CardContent>
                  <ul className="list-disc list-inside text-sm text-muted-foreground">
                    {item.summary.map((b, i) => <li key={i}>{b}</li>)}
                  </ul>
                </CardContent>
                <CardFooter className="flex justify-between items-center">
                  <span className="text-sm font-medium text-primary">Relevance: {item.relevance}</span>
                  <Button asChild variant="secondary" size="sm">
                    <a href={item.link} target="_blank" rel="noopener noreferrer">
                      <BookOpen className="mr-2 h-4 w-4" />
                      Read More
                    </a>
                  </Button>
                </CardFooter>
              </Card>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
